 import {
   playMode
 } from 'common/js/config';
 import {
   loadSearch,
   loadPlayHistory,
   loadFavoriteList
 } from 'common/js/cache';
 const state = {
   isLogin: false,
   currentUser: '',
   singer: {},
   //  
   playing: false,
   fullScreen: false,
   playlist: [], //
   sequenceList: [], //
   mode: playMode.sequence, //  
   currentIndex: -1, //
   disc: {},
   toplist: {},
   searchHistory: loadSearch(),
   playHistory: loadPlayHistory(),
   favoriteList: loadFavoriteList()
 }

 export default state



